export default {
	data() {
		return {
			// 图
			showFilter: false,
			rangeValues: [0, 100],
			slideWidth: 450,
			slideHeight: 80,
			slideBlockSize: 40,
			slideMin: 0,
			slideMax: 100,
			isLiveMode: true,
			step: 1,
			//
			timeMinValue: 0,
			timeMaxValue: 10,
			
			rangeValues2: [1, 6],
			serTime: '02:24:00-14:24:00',
			
			rangeValues3: [3, 5],
			// 
			partTimeList: 0,
			loadBody: true,
			isEnd: 0,
			isTop: 1,
			showLoad: false, 
			targetY: '',
			startY: '',
			endY: '',
			showModal: false,
			contentFixed: false,
			target: true,
			UserInfo: "",
			UserPartTimePrefer: ['全部零活', '游戏代练', '睡觉'],
			pageTop: '',
			freshY: '',
			freshTop: 0,
			startYFresh: '',
			freshAllow: false,
			showFresh: true,
			reachFresh: false
		};
	},
	onPullDownRefresh() {
		this.showLoad = true
	},
	mounted() {
		var that = this
		// 距离
		
		// 图
		
		// 
		setTimeout(() => {
			this.loadBody = true
			this.partTimeList = 10
		}, 1)
		const query = uni.createSelectorQuery().in(this)
		query.select('#content-wrapper').boundingClientRect(data => {
			console.log(data);
			that.pageTop = data.top
		}).exec(() => {}); 

		
	},
	
	components: {
		Loading6,
		refresh,
		RangeSlider
	},
	computed: {
		freshStyle(){
			var transitionY = this.freshTop
			// console.log(transitionY);
			var style = `transform: translateY(${transitionY-50}px);`
			return style
		}
	},
	methods: {
		// 图标
		CloseFilter(){
			this.showFilter = !this.showFilter
			// this.$emit("PageScroll", 300)
			
		},
		pad: function(num, n) {
			return Array(n - ('' + num).length + 1).join(0) + num;
		},
		onRangeChange: function(e) {
			this.rangeValues = [e.minValue, e.maxValue];
		
			console.log(this.rangeValues);
			console.log(JSON.stringify(e));
		},
		test: function() {
			this.rangeValues = [4.2, 6.6];
		},
		onRangeChange2: function(e) {
			let startTime = this.formatTimeBySliderValue(e.originalValue.minValue);
			let endTime = this.formatTimeBySliderValue(e.originalValue.maxValue);
			this.serTime = startTime + '-' + endTime;
		},
		onRangeChange3: function(e) {
			this.rangeValues3 = [e.minValue, e.maxValue];
		},
		formatTimeBySliderValue(value) {
			//按比例，将滑块上面的数值进行转换为时间形式
			//转换为分钟数
			let minutes = (24 * 60 * value) / this.slideMax;
			//转换为小时数
			let hours = parseInt(minutes / 60);
			//剩余分钟数
			let minutes_min = parseInt(minutes % 60);
			return '' + this.pad(hours, 2) + ':' + this.pad(minutes_min, 2) + ':' + '00';
		},
		// 
		checkIsSoterEnrolledInDeviceFingerPrint() {
		    uni.checkIsSoterEnrolledInDevice({
		        checkAuthMode: 'fingerPrint',
		        success(res) {
		            console.log(res);
		        },
		        fail(err) {
		            console.log(err);
		        },
		        complete(res) {
		            console.log(res);
		        }
		    })
		},
		startSoterAuthenticationFingerPrint() {
		    uni.startSoterAuthentication({
		        requestAuthModes: ['fingerPrint'],
		        challenge: '123456',
		        authContent: '请用指纹解锁',
		        success(res) {
		            console.log(res);
		        },
		        fail(err) {
		            console.log(err);
		        },
		        complete(res) {
		            console.log(res);
		        }
		    })
		},
		FingerLogin(){
			uni.startSoterAuthentication({
				requestAuthModes: ['fingerPrint'],
				challenge: '123123',
				authContent: "是否开启指纹登录",
				success(res){
					console.log(res)
				}
			})
		},
		isRefresh(){
			var that = this
			that.$refs.refresh.endAfter()
			that.showLoad = true
			setTimeout(() => {
				that.showLoad = false
			}, 2000)
		},
		ChangeTop(data){
			this.isTop = data
		},
		TouchMoveFresh(e){
			// 
			var that = this
			this.$refs.refresh.refreshMove(e);
		},
		TouchEndFresh(e){ 
			this.$refs.refresh.refreshEnd(e);

			
			
		},
		TouchStartFresh(e){
			var that = this
			this.$refs.refresh.refreshStart(e);
			// that.startY = e.changedTouches[0].clientY
			// this.$nextTick(function(){
				
			// 	const query = uni.createSelectorQuery().in(this)
			// 	query.select('#content-wrapper').boundingClientRect(data => {
			// 		if(data.top == that.pageTop){
			// 			that.isTop = 1
			// 			console.log(that.isTop);
						
			// 		} else {
			// 			that.isTop = 0
			// 		}
			// 	}).exec(() => {}); 
			// })
			
			
		},
		TouchStart(e){
			// this.startY = e.changedTouches[0]
		},
		TouchEnd(e){
			// this.endY = e.changedTouches[0].pageY
		},
		ShowModal(data){
			this.showModal = data
			
		},
		ViewParttimeDetail(index){
			uni.navigateTo({
				url: '../parttime-detail/parttime-detail?id=' + index
			})
		},
		ViewPoster(){
			
		},
		ViewDetail(){
			var url = this.target == true ? '../parttime-detail/parttime-detail' : '../fulltime-detail/fulltime-detail'
			uni.navigateTo({
				url
			})
		},
		GoFulltimeJob(){
			uni.pageScrollTo({
			scrollTop: 0,
			duration: 0
			});
			this.target = !this.target
			
		},
		GoMap(){






			uni.redirectTo({
				url: '../take-job-mapR/take-job-mapR'
			})

			
		},
	}
}
